import { SET_AUTOCOMPLETE_HANDLE, SET_AUTOCOMPLETE_LIST, SET_ERROR_MESSAGE_ACITON, YOUTUBE_AUTOCOMPLETE } from "../../../../../actions/addFeedActions";
import { AUTOCOMPLETE } from "../../../../../actions/manageFeedsAction";
import { formatTemplateText, isPersInstaAcc, isValidUrl } from "../../../../../utils";
import store from "../../../../../store";

export const GET_CHILD_ACCOUNTS = (accounts, networkId, filter = "") => {
    const isPer = networkId != 18 ? false : isPersInstaAcc(parseInt(filter));
    if (networkId == 10 || networkId == 20 || networkId == 4 || (networkId == 3 && filter != 1) || (networkId == 18 && !isPer)) {
        return accounts?.reduce((acc, account) => {
            if (account?.ConnectedAccounts?.child?.length) {
                acc = [...acc, ...account.ConnectedAccounts.child];
            }
            return acc;
        }, []) || [];
    }
    return accounts?.[0]?.Network?.id == (isPer ? 2 : networkId) ? accounts : []
}

export const AUTOCOMPLETE_INPUT = (value, networkId, filter = "", latlng = "") => (dispatch) => {
    switch (parseInt(networkId)) {
        case 3:
            dispatch(FB_AUTOCOMPLETE(value))
            break;

        case 1:
            dispatch(TWITTER_AUTOCOMPLETE(value))
            break;

        case 7:
            dispatch(YT_CHANNEL_AUTOCOMPLETE(value, filter))
            break;

        case 19: 
            dispatch(YELP_AUTOCOMPLETE(value, latlng));
            break;

        default:
            break;
    }
}

const FB_AUTOCOMPLETE = (value) => (dispatch) => {
    const obj = { networkId: 3, handle: value, filterType: 8, showHandle: false }
    AUTOCOMPLETE(obj).then(({ data }) => {
        dispatch(SET_AUTOCOMPLETE_HANDLE(data.responseCode === 200 ? data.response.data : []))
    }).catch((error) => {
        dispatch(SET_AUTOCOMPLETE_HANDLE([]))
    })
}

const TWITTER_AUTOCOMPLETE = (value) => (dispatch) => {
    const obj = { networkId: 1, handle: value }
    AUTOCOMPLETE(obj).then(({ data }) => {
        if (data.responseCode === 200) {
            const { responseData, responseDataUserList } = data;
            dispatch(SET_AUTOCOMPLETE_HANDLE(responseData.data));
            dispatch(SET_AUTOCOMPLETE_LIST(responseDataUserList?.data));
        } else {
            dispatch(SET_AUTOCOMPLETE_HANDLE([]));
            dispatch(SET_AUTOCOMPLETE_LIST([]));
        }
    });
}

const YT_CHANNEL_AUTOCOMPLETE = (value, filter) => (dispatch) => {
    if ([11, 1, 75].includes(parseInt(filter))) {
        const type = filter == 11 ? "playlist" : "channel"
        const obj = {
            handle: value,
            networkId: 7,
            filterType: type
        }
        YOUTUBE_AUTOCOMPLETE(obj).then(res => {
            const { data, status } = res
            dispatch(SET_AUTOCOMPLETE_HANDLE(status === 200 ? data?.listItems : []))
        }).catch(ex=>{
            dispatch(SET_AUTOCOMPLETE_HANDLE([]))
        })
    }
    else return
}

const YELP_AUTOCOMPLETE = (value, latlng) => (dispatch) => {
    const obj = {
        "handle": value,
        "networkId": 19,
        "filterType": 28,
        "Latlng": latlng
    }
    // return new HttpClient().post(`${APP_API_URL}api/v1/feed/feeds/autocomplete`, { handle: searchQuery, networkId: 7, filterType: type });
    AUTOCOMPLETE(obj).then((response) => {
        dispatch(SET_AUTOCOMPLETE_HANDLE(response?.data?.responseData ?? []))
    }).catch((error) => {
        dispatch(SET_AUTOCOMPLETE_HANDLE([]))
    })
}

export const RESET_AUTOCOMPLETE = () => (dispatch) => {
    dispatch(SET_AUTOCOMPLETE_HANDLE([]));
    dispatch(SET_AUTOCOMPLETE_LIST([]));
}

export const getFilterName = (filter) => {
    const { networkTab } = store.getState().addFeedReducer.networkDetails
    if (!networkTab) return
    return networkTab.find(tab => tab.filter == filter)?.feedName || "";
}


export const getFeedAlertText = (networkId) => (dispatch, getState) => {
    const { personalAccount, feedObj } = getState().addFeedReducer;
    const { filter } = feedObj;
    const FEED_ALERT_TEXT = window?.APP_CONTENT?.add_feed?.alert_msg || {};
    if (networkId == 7 || networkId == 5 || networkId == 8 || networkId == 6 || networkId == 11) return "";
    // Networks that always return empty
    if ([5, 6, 7, 8, 11].includes(networkId)) return "";
    if(networkId == 10 && [1, 2, 17].includes(filter)) return "";

    // Special case for network 28 + filter 72 + personal account 27
    if (networkId === 28 && filter === 72 && personalAccount?.[0]?.Network?.id === 27) {
        return FEED_ALERT_TEXT["28_special"];
    }

    // Network 1 (Twitter) special case
    if (filter === 1 && networkId === 1) return FEED_ALERT_TEXT["1_twitter"];

    // Other default network cases
    if (filter === 1) return FEED_ALERT_TEXT["1_default"];
    if (filter === 2) return FEED_ALERT_TEXT["2_default"];

    // Lookup by filter directly
    return FEED_ALERT_TEXT[filter] || "";
}

export const feedByNet = (feeds, networkId) => {
    return feeds?.reduce((acc, curr) => {
        if(curr.networkId == networkId) acc++;
        return acc;
    }, 0) || 0;
}

export const feedByNetFilter = (feeds, networkId, filter) => {
    return feeds?.reduce((acc, curr) => {
        if(curr.networkId == networkId && curr.filterId == filter) acc++;
        return acc;
    }, 0) || 0;
}

export const inputValidationByNetwork = (networkId) => (dispatch, getState) => {
    const { feedObj } = getState().addFeedReducer
    const { feedText, secondText, filter, field33placeId, feed33, field33placeName } = feedObj;
    const appContent = window?.APP_CONTENT?.common || {};

    let errorObj = { show: false, showSecond: false, message: "" };
    switch (parseInt(networkId)) {
        case 18:
            if ([26, 23].includes(parseInt(filter))) {
                if (feedText == "" || !feedText) errorObj = { show: true, message: appContent?.field_req ?? "" }
                if (feedText[feedText.length - 1] == '@' || feedText[feedText.length - 1] == "#" || feedText.includes("@") || feedText.includes("#")) errorObj = { show: true, message: appContent?.hash_hand_not_allowed ?? "" }
            }
            break;

        case 28:
            if ([72, 70, 71].includes(parseInt(filter))) {
                if (feedText == "" || !feedText) errorObj = { show: true, message: appContent?.field_req ?? "" }
            }
            break;

        case 1:
            if (feedText == "" || !feedText) errorObj = { show: true, message: appContent?.field_req ?? "" }
            if (feedText[feedText.length - 1] == '@' || feedText[feedText.length - 1] == "#" || feedText.includes("@") || feedText.includes("#")) errorObj = { show: true, message: appContent?.hash_hand_not_allowed ?? "" }
            break;

        case 4:
            if (filter == 29) return
            else if (!feed33 || !field33placeId || !field33placeName) errorObj = { show: true, message: appContent?.field_req ?? "" }
            break;
        
        case 23:
            if (feedText == "" || !feedText) errorObj = { show: true, message: appContent?.field_req ?? "" }
            else if(!isValidUrl(feedText)) errorObj = { show: true, message: appContent?.valid_url ?? "" }
            break;
        
        case 15:
            if (feedText == "" || !feedText) errorObj = { show: true, message: appContent?.field_req ?? "" }
            if (secondText == "" || !secondText) errorObj = {...errorObj, showSecond: true, message: appContent?.field_req ?? "" }
            break;
        
        case 19:
            if (filter == 27 && (!isValidUrl(feedText))) errorObj = { show: true, message: feedText?.length > 0 ? appContent?.valid_url ?? "" : appContent?.field_req ?? "" }
            if(filter == 28) {
                if(feedText == "" || !feedText)  errorObj = { show: true, message: appContent?.field_req ?? "" }
                if (secondText == "" || !secondText) errorObj = {...errorObj, showSecond: true, message: appContent?.field_req ?? "" }
            }
            break;

        case 12:
            if (feedText == "" || !feedText) errorObj = { show: true, message: appContent?.field_req ?? "" }
            if(!isValidUrl(secondText)) errorObj = { ...errorObj, showSecond: true, message: secondText?.length > 0 ? appContent?.valid_url ?? "" : appContent?.field_req ?? "" }
            break;

        case 10:
            if (filter == 16) {
                if (!isValidUrl(feedText)) errorObj = { show: true, message: appContent?.valid_url ?? "" }    
            } else if (filter == 1 || filter == 2 || filter == 17) {
                if (feedText == "" || !feedText) errorObj = { show: true, message: appContent?.field_req ?? "" }    
            }
            break;

        case 35:
            if (!isValidUrl(feedText)) errorObj = { show: true, message: appContent?.valid_url ?? "" } 
            break;

        case 47:
            if (feedText == "" || !feedText) errorObj = { show: true, message: appContent?.field_req ?? "" } 
            break;

        case 5:
        case 7:
        case 6:
        case 8:
        case 3:
        case 11:
            if (networkId == 3 && (filter == 1 || filter == 62 || filter == 55 || filter == 66)) return
            if (feedText == "" || !feedText) errorObj = { show: true, message: appContent?.field_req ?? "" }
            // if (feedText[feedText.length - 1] == '@' || feedText[feedText.length - 1] == "#" || feedText.includes("@") || feedText.includes("#")) errorObj = { show: true, message: "@ and # not allowed" }
            break;

        default:
            break;
    }

    if (errorObj.show || errorObj.showSecond) {
        dispatch(SET_ERROR_MESSAGE_ACITON(errorObj))
        return true
    }
    return false
}

export const getInputIcon = (filterId, networkId) => {
    switch (parseInt(filterId)) {
        case 26:
        case 72:
        case 2:
            return "hashtag"

        case 71:
        case 23:
        case 1:
        case 7:
        case 75:
        case 3:
        case 4: return ([4, 47].includes(parseInt(networkId)) && filterId == 4) ? "text" : "at"

        case 30: case 70: case 77: case 84: case 83: case 78: case 80: case 90:
            return "link"
        case 8: case 17:
            return "user"
        case 5:
            return "magnifying-glass"
        case 33:
            return "globe"
        case 12:
        case 56: case 16:
            return "flag"
        case 11:
            return "list"
        case 67:
            return "location-dot"
        case 22:
            return "users"
        case 13: case 27: case 28:
            return "font"
        case 89:
            return "list"

        default:
            return
    }
}

export const getInputPlaceholder = (filterData, networkId) => {
    const pageContent = window?.APP_CONTENT?.add_feed?.place_holders || {};
    if(networkId == 23) return pageContent["23"] ?? "";
    if([8, 11, 13, 27, 65, 67, 28, 78, 80, 90].includes(parseInt(filterData.filter))) return pageContent[filterData.filter] ?? "";
    else return formatTemplateText(pageContent["default"] ?? "", { feedType: `${networkId == 27 ? "trending " : ""}${filterData?.feedtype || ""}` });
}

export const isAuthRequired = (networkId, filterId) => {
    if(networkId == 10 && filterId == 86) return true;
    else if(networkId == 18 && filterId != 30) return true;
    else if(networkId == 4 && filterId == 29) return true;
    else if(networkId == 1) return true;
    else if(networkId == 3 && filterId != 2) return true;
    else if(networkId == 8) return true;
    else if(networkId == 20) return true;
    else if(networkId == 28 && ([1, 73].includes(filterId))) return true;
    else if(networkId == 47) return true;
    return false;
}